.footer {
 background: $color-alabaster;
 bottom: 0;
 left: 0;
 padding: 5rem 0;
 position: fixed;
 right: 0;
 z-index: -1;

 .container {
 padding: 0 4rem;
 @media (min-width: $breakpoint-xlarge) {
 padding: 0 3rem;
 }
 }
 .footer__row {
 @media (min-width: $breakpoint-medium) {
 display: flex;
 }
 }
}

.footer__section {
 margin-bottom: 3rem;

 @media (min-width: $breakpoint-medium) {
 margin-bottom: 0;
 width: 33.33333%;
 }
}