.project {
 position: relative;

 .container {
 max-width: 100%;
 position: relative;
 }

 .project__description {
 @media (min-width: $breakpoint-large) {
 width: 40%;
 position: sticky;
 top: 36px;
 margin-right: 5%;
 float: right;
 @include clearfix;
 }

 a {
 text-decoration: underline;
 }
 }

 .h2 {
 margin: 3rem 0 0.5rem;

 &:first-of-type {
 margin-top: 0;
 }
 }
}

.project__assets {
 margin-left: -3rem;
 margin-right: -3rem;

 @media (min-width: $breakpoint-large) {
 width: 55%;
 }
}

.project-meta {
 margin: 3rem 0 3rem 0;

 @media (min-width: $breakpoint-large) {
 margin: 5rem 0 3rem 0;
 max-width: 70%;
 }
}



.project__link {
 text-decoration: underline;
}

.asset--img {
 width: 100%;
}

.project__navigation {
 background: $color-grey-dark;
 padding: 5rem 0;
}

.navigation__row {
 @media (min-width: $breakpoint-small) {
 display: flex;
 justify-content: space-between;
 }
}

.navigation__link {
 color: $color-white;
 display: inline-block;

 &:nth-of-type(1),
 &:nth-of-type(2) {
 display: table;
 margin: 0 auto;
 text-align: center;

 @media (min-width: $breakpoint-small) {
 display: inline-block;
 margin: 0;
 text-align: left;
 }
 }


 &:nth-of-type(2) {
 margin-top: 3rem;

 @media (min-width: $breakpoint-small) {
 margin-top: 0;
 margin-left: auto;
 }
 }
}

.link__label {
 &,
 &:after,
 &:before {
 position: relative;
 left: 0;
 -webkit-transition: all 0.3s ease-in-out 0s;
 transition: all 0.3s ease-in-out 0s;
 }

 .link--next &:after {
 content: "\02192";
 }
 .link--prev &:before {
 content: "\02190";
 }

 .navigation__link.link--prev:hover & {
 left: 7px;
 &:before {
 left: -7px;
 }
 }

 .navigation__link:hover &:after {
 left: 7px;
 }
}

.link__title {
 display: inline-block;
 font-size: 1.8rem;
 position: relative;

 &:after {
 content: "";
 position: absolute;
 width: 100%;
 height: 1px;
 bottom: -4px;
 left: 0;
 background-color: $color-white;
 visibility: hidden;
 -webkit-transform: scaleX(0);
 transform: scaleX(0);
 -webkit-transition: all 0.3s ease-in-out 0s;
 transition: all 0.3s ease-in-out 0s;
 }

 .navigation__link:hover &:after {
 visibility: visible;
 -webkit-transform: scaleX(1);
 transform: scaleX(1);
 }
}

.post {

 .project__description {
 @media (min-width: $breakpoint-medium) {
 max-width: 740px;
 padding-left: 20px;
 padding-right: 20px;
 margin-left: auto;
 margin-right: auto;
 }
 }

 .project-meta {
 @media (min-width: $breakpoint-large) {
 margin: 3rem 0 3rem 0;
 max-width: 100%;
 }
 }
}