.container {
 display: block;
 margin: 0 auto;
 max-width: 70em;
 padding: 0 3rem;
 @include clearfix;
 position: relative;
}

.header {
 // background: $color-white;
 padding: 3rem 0;

 margin-bottom: 7rem;
 @media (min-width: $breakpoint-medium) {
 margin-bottom: 6rem;
 padding-top: 7rem;
 }
 @media (min-width: $breakpoint-large) {
 margin-bottom: 12rem;
 padding-top: 15rem;
 }

 .body--post & {
 margin-bottom: 7rem;
 }
}

.logo {
 font-size: 1.8rem;
 font-weight: 600;
 margin: 0;
 line-height: 1;
 display: flex;
 width: 100%;
 justify-content: space-between;
 flex-direction: column;

 @media (min-width: $breakpoint-medium) {
 flex-direction: row;
 }
}
.logo__link {
 font-family: $font-secondary;
 font-size: 5rem;
 font-weight: 800;
 margin: 0;
 line-height: 5.5rem;
 position: relative;
 display: flex;
 flex-wrap: wrap;
 justify-content: space-between;
 width: 11rem;
 user-select: none;
 span {
 width: 5rem;
 text-align: center;
 }
}
.logo_ipfs {
 font-variant: all-small-caps;
 opacity: .5;
 padding: 2rem 1rem;

 @media (min-width: $breakpoint-medium) {
 padding: 1.8rem 0;
 }
}

.tooltip {
 position: relative;
 cursor: help;

 &:before,
 &:after {
 opacity: 0;
 visibility: hidden;
 transition: opacity .2s;

 top: 100%;
 right: 0;
 }
 &:before {
 content: attr(data-tooltip);
 position: absolute;
 color: #fff;
 background: #000;
 border-radius: 2px;
 padding: 6px 6px 10px;
 margin-top: 8px;
 }
 &:after {
 position: absolute;
 content: "";
 width: 0;
 height: 0;
 margin-top: 3px;
 border-left: 4px solid transparent;
 border-right: 4px solid transparent;
 border-bottom: 6px solid #000;
 }
 &:hover:before,
 &:hover:after,
 &:focus:before,
 &:focus:after {
 opacity: 1;
 visibility: visible;
 }
}

.main {
 background: $color-white;
 //padding: 7rem 0 0;
 margin-bottom: 37rem;
 position: relative;
 top: 0;
 left: 0;
 z-index: 1;

 @media (min-width: $breakpoint-medium) {
 //padding: 21rem 0 0;
 margin: 2rem;
 margin-bottom: 21rem;
 }
 @media (min-width: $breakpoint-large) {
 margin: 4rem;
 margin-bottom: 21rem;
 }
}
.main--post {
 background-size: 100% 21rem;
 background-repeat: no-repeat;
}

.section {
 padding: 0 0 8rem;
}
.section--xs {
 padding: 0 0 2rem;
}
.section--s {
 padding: 0 0 4rem;
}
.section--l {
 padding: 0 0 12rem;
}

.intro-text {
 font-weight: 500;
 @media (min-width: $breakpoint-medium) {
 font-size: 2.7rem;
 max-width: 70%;
 }

 a {
 text-decoration: underline;
 }
}



.projects-grid {
 display: flex;
 flex-wrap: wrap;

 @media (min-width: $breakpoint-small) {
 margin-left: -1.5rem;
 margin-right: -1.5rem;
 }
}

.project-block {
 margin-bottom: 3rem;
 display: block;
 width: 100%;

 &:last-child {
 margin-bottom: 0;
 }

 @media (min-width: $breakpoint-small) {
 width: 50%;
 padding: 0 1.5rem;
 }
 @media (min-width: $breakpoint-medium) {
 width: 33.33333%;
 }
}
.project-block__container {
 width: 100%;
 padding-top: 100%;
 position: relative;
}

.project-block__img-wrapper {
 position: absolute;
 top: 0; right: 0; bottom: 0; left: 0;
 z-index: 1;
}

.project-block__img {
 width: 100%;
 opacity: 1;
 z-index: 1;
 // filter: url("#ripple");
 // margin-left: -20px;
 // margin-top: -20px;
}

.project-block__img-overlay {
 opacity: 0.05;
 transition: opacity .4s ease-in-out;
 position: absolute;
 top: 0; right: 0; bottom: 0; left: 0;
 z-index: 2;

 &:hover {
 opacity: 1;
 }

 &.is-visible {
 opacity: 1;
 transition: none;
 }
}

.project-block__title {
 color: $color-black;
 font-family: $font-primary;
 font-size: 1.8rem;
 font-weight: 500;
 line-height: 1;
 margin: 0;
}

.project-block__subtitle,
.post-block__subtitle {
 color: $color-silver-chalice;
 font-family: $font-secondary;
 font-size: 1rem;
 font-weight: 400;
 letter-spacing: 0.2rem;
 line-height: 1;
 text-transform: uppercase;
 margin: 1rem 0 0.5rem;
}

.posts-list {
 @media (min-width: $breakpoint-small) {
 margin-left: -1.5rem;
 margin-right: -1.5rem;
 }
}

.posts-list__link {
 text-decoration: underline;
 font-weight: 300;
 font-size: 1.6rem;
 letter-spacing: 0.1rem;

 @media (min-width: $breakpoint-small) {
 padding: 0 1.5rem;
 }
}

.post-block {
 margin-bottom: 2.2rem;
 display: block;

 &:last-child {
 margin-bottom: 0;
 }

 @media (min-width: $breakpoint-small) {
 padding: 0 1.5rem;
 }

 h3 {
 margin: 2rem 0 0 0;
 }
 .h1 {
 font-size: 2.2rem;
 font-weight: 500;
 }
}

.post-block__source {
 margin: 1rem 0 0.5rem;
}

.post-badge {
 background: $color-silver;
 color: $color-white;
 border-radius: 3px;
 font-family: $font-secondary;
 font-size: 1rem;
 font-weight: 500;
 white-space: nowrap;
 padding: 0.2rem 0.4rem;
 vertical-align: 0.3rem;
 margin-left: .5rem;
}


.archive-item {
 display: block;
 padding: 1rem 0;
}

.archive-item__img {
 position: absolute;
 visibility: hidden;
 opacity: 0;
 transition: all 0.5s ease-in-out;
 right: 0px;
 width: 33.33333%;
 height: auto;
 transform: translateY(-50%);

 .archive-item:hover &,
 .post-block:hover & {
 visibility: visible;
 opacity: 1;
 }
}

.archive-item__icon {
 display: inline-block;
 width: 1rem;
 margin-left: 6px;
 filter: grayscale(1);
}

.canvas-background {
 position: absolute;
 top: 0;
 right: 0;
 bottom: 0;
 left: 0;
}
