/* Fonts
 ****************************************************************/

$font-default: Helvetica, Arial, sans-serif;
$font-primary: 'Radnika', $font-default;
$font-secondary: 'Now', $font-default;
$font-text: "content-serif-font", Georgia, Cambria, "Times New Roman", Times, serif;

/* Colors
 ****************************************************************/

$color-white: #fff;
$color-black: #000;
$color-grey-dark: #222;
$color-silver: #999;
$color-silver-chalice: #aaa;
$color-alabaster: #f5f5f5;
$color-tango: #f46f25;
$color-white-sand: #f6f6f6;

/* Breakpoint
 ****************************************************************/

$breakpoint-xsmall: 400px;
$breakpoint-small: 550px;
$breakpoint-medium: 750px;
$breakpoint-large: 1000px;
$breakpoint-xlarge: 1200px;