.header--post {
 height: 18rem;
 background-size: cover;
 background-position: center;
 margin-bottom: 3rem;
}

.post {
 position: relative;
 margin-bottom: 8rem;

 .article-wrapper {
 @media (min-width: $breakpoint-medium) {
 max-width: 740px;
 padding-left: 20px;
 padding-right: 20px;
 margin-left: auto;
 margin-right: auto;
 }
 }

 .post-meta {
 margin: 3rem 0 3rem 0;

 @media (min-width: $breakpoint-medium) {
 margin: 5rem auto 3rem auto;
 max-width: 700px;
 }
 }

 .h2 {
 margin: 3rem 0 0.5rem;

 &:first-of-type {
 margin-top: 0;
 }
 }

 .h1 {
 a {
 text-decoration: underline;
 }
 }

 li {
 margin-bottom: 0rem;

 &:last-child {
 margin-bottom: 0;
 }
 }

 img {
 width: 100%;
 }
}

.post-navigation {
 background: $color-black;
 padding: 5rem 0;

 a {
 color: $color-white;
 display: inline-block;

 &:hover {
 h2:after {
 visibility: visible;
 -webkit-transform: scaleX(1);
 transform: scaleX(1);
 }

 &.prev {
 span {
 left: 7px;
 }
 }

 span {
 &:after {
 left: 7px;
 }

 &:before {
 left: -7px;
 }
 }
 }

 &:nth-of-type(1),
 &:nth-of-type(2) {
 display: table;
 margin: 0 auto;
 text-align: center;

 @media (min-width: $breakpoint-xsmall) {
 display: inline-block;
 margin: 0;
 text-align: left;
 }
 }

 &:nth-of-type(1) {
 @media (min-width: $breakpoint-xsmall) {
 float: left;
 }
 }

 &:nth-of-type(2) {
 margin-top: 3rem;

 @media (min-width: $breakpoint-xsmall) {
 float: right;
 margin-top: 0;
 }
 }

 &.next {
 span {
 &:after {
 content: "\02192";
 }
 }
 }

 &.prev {
 span {
 position: relative;
 left: 0;
 -webkit-transition: all 0.3s ease-in-out 0s;
 transition: all 0.3s ease-in-out 0s;

 &:before {
 content: "\02190";
 }
 }
 }
 }

 span {
 &:after,
 &:before {
 position: relative;
 left: 0;
 -webkit-transition: all 0.3s ease-in-out 0s;
 transition: all 0.3s ease-in-out 0s;
 }
 }

 h2 {
 display: inline-block;
 font-size: 1.8rem;
 position: relative;

 &:after {
 content: "";
 position: absolute;
 width: 100%;
 height: 1px;
 bottom: -4px;
 left: 0;
 background-color: $color-white;
 visibility: hidden;
 -webkit-transform: scaleX(0);
 transform: scaleX(0);
 -webkit-transition: all 0.3s ease-in-out 0s;
 transition: all 0.3s ease-in-out 0s;
 }
 }
}

.article-wrapper {
 font-size: 20px;
 text-rendering: optimizeLegibility;
 -webkit-font-smoothing: antialiased;

 .article-meta {
 margin-top: 24px;
 margin-bottom: 24px;
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6,
 figcaption {
 font-family: $font-primary;
 line-height: 1.25;
 text-transform: none;
 color: rgba(0, 0, 0, 0.84);
 }
 p,
 ul,
 li,
 a,
 blockquote,
 div {
 font-family: $font-text;
 line-height: 1.5;
 color: rgba(0, 0, 0, 0.84);
 }

 h1 {
 font-weight: 700;
 font-style: normal;
 font-size: 42px;
 margin-left: -2.63px;
 line-height: 1.04;
 letter-spacing: -0.015em;
 padding-top: 5px;
 }
 h2 {
 margin-top: 63px;
 font-weight: 700;
 font-style: normal;
 font-size: 42px;
 margin-left: -2.63px;
 line-height: 1.04;
 letter-spacing: -0.015em;
 }
 h3 {
 margin-top: 56px;
 font-weight: 700;
 font-style: normal;
 font-size: 34px;
 margin-left: -2.13px;
 line-height: 1.15;
 letter-spacing: -0.015em;
 }
 h4 {
 font-size: 26px;
 margin-left: -1.63px;
 line-height: 1.22;
 letter-spacing: -0.012em;
 font-style: normal;
 font-weight: 600;
 margin-top: 39px;
 }
 h5 {
 font-size: 18px;
 margin-left: -1.13px;
 line-height: 1.29;
 letter-spacing: -0.009em;
 font-style: normal;
 font-weight: 600;
 margin-bottom: -22px;
 margin-top: 39px;
 }
 p {
 margin-top: 38px;
 font-weight: 400;
 font-style: normal;
 font-size: 21px;
 line-height: 1.58;
 letter-spacing: -0.003em;
 }
 a {
 text-decoration: underline;
 }
 blockquote {
 border-left: 3px solid rgba(0, 0, 0, 0.84);
 padding-left: 20px;
 padding-bottom: 2px;
 font-style: italic;
 font-size: 21px;
 line-height: 1.58;
 letter-spacing: -0.003em;
 margin-top: 29px;
 font-weight: 400;
 }
 small {
 font-size: 80%;
 line-height: 1;
 display: inline-block;
 }
 img + em {
 display: block;
 text-align: center;
 font-weight: 400;
 font-style: normal;
 font-feature-settings: "liga" on, "lnum" on;
 -moz-font-feature-settings: "liga" on, "lnum" on;
 -webkit-font-feature-settings: "liga" on, "lnum" on;
 font-size: 16px;
 line-height: 1.4;
 color: rgba(0, 0, 0, 0.68);
 margin-top: 10px;
 }
 figure {
 margin: 0;
 margin-top: 38px;
 }
 figcaption {
 font-weight: 400;
 font-size: 16px;
 line-height: 1.4;
 color: rgba(0,0,0,.68);
 letter-spacing: 0;
 text-align: center;
 margin: 10px auto 0;
 }
 ul {
 margin: 0;
 }
 ul {
 list-style-type: none;
 }
 ul > li:before {
 content: "– ";
 }
 li .indent {
 margin-left:0.7em;
 display:inline-block;
 }
 img.wide {
 width: 100vw;
 max-width: 100vw;
 margin-left: 50%;
 transform: translate(-50%, 0);
 @media (min-width: $breakpoint-small) {
 width: 90vw;
 max-width: 90vw;
 }
 }

 .v-container {
 margin-top: 38px;
 }

 pre {
 white-space: pre-wrap;
 }
}
