.link {
 text-decoration: underline;
 font-weight: 300;
 font-size: 1.6rem;
 letter-spacing: 0.1rem;
}

[data-title] {
 position: relative;
 display: inline-block;
 &:after {
 content: attr(data-title);
 position: absolute;
 background: $color-white;
 border: 1px solid $color-silver-chalice;
 border-radius: 3px;
 font-family: $font-secondary;
 font-size: 1.2rem;
 font-weight: 400;
 white-space: nowrap;
 padding: 0.4rem 0.6rem;
 margin-top: .5rem;
 top: 100%;
 left: 0;
 visibility: hidden;
 pointer-events: none;
 opacity: 0;
 transition: .4s ease-in-out;
 }
 &:hover:after {
 visibility: visible;
 opacity: 1;
 }
}

.v-container {
 position: relative;
 width: 100%;
 height: 0;
 padding-bottom: 56.25%;

 &--3\:2 {
 padding-bottom: 66.67%;
 }
}
.video {
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
}
.grayscale {
 filter: grayscale(1);
}